.nonlabviewtable {
  height: calc(640px - 50px);
  overflow-y: auto;
  border: 1px solid rgba(238, 238, 238, 1);
}

.nonlabtebletitlecell {
  display: flex;
  align-items: center;
  padding: 0px 5px;
}

.nonlabteblecell {
  display: flex;
  align-items: center;
  border: rgba(238, 238, 238, 1) 1px solid;
  padding: 0px 5px;
  border-collapse: collapse;
}
