.wardcarddetileditbutton {
  height: 25px;
  width: 25px;
  border-radius: 99em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(59, 151, 225, 0.5);
  &:hover {
    background-color: rgba(59, 151, 225, 1);
  }
}

.wardcarddetailborderclick {
  border-radius: 4px;
  border: 1px solid rgba(245, 166, 35, 1);
}

.wardcarddetailborderunclick {
  border-radius: 4px;
  border: 1px solid rgba(215, 238, 255, 1);
}

.patientviewbutton {
  height: 20px;
  border-radius: 4px;
  background-color: rgba(59, 151, 225, 0.5);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
  position: absolute;
  right: 8px;
  color: white;
  padding: 2px 4px;
  z-index: 100;
  &:hover {
    background-color: rgba(59, 151, 225, 1);
    color: white;
  }
}

.modulecontent{
  display: grid;
  grid-template-columns: 69px 136px; 
  grid-column-gap: 10px;
  .sparetext{
    text-align: justify;
  }
}

.WardcardModuleDeleteBtn{
  border: 1px solid rgba(208,2,27,1);
  color: rgba(208,2,27,1);
}

.WardcardModuleDeleteBtnDisable{
  border: 1px solid rgba(208,2,27,1);
  color: rgba(208,2,27,1);
}